@font-face {
  font-family: SFProDisplay;
  src: url(assets/fonts/FontsFree-Net-SFProDisplay-Regular.ttf);
}

@font-face {
  font-family: SFProText;
  src: url(assets/fonts/FontsFree-Net-SFProText-Regular.ttf);
}

@font-face {
  font-family: SFUIText;
  src: url(assets/fonts/SF-UI-Text-Regular.ttf);
}

@font-face {
  font-family: SFUIDisplay;
  src: url(assets/fonts/sf-ui-display-medium-58646be638f96.otf);
}

* {
  font-family: SFProDisplay, sans-serif !important;
}

.font12 {
  font-size: 12pt;
}
.font13 {
  font-size: 13pt;
}
.font14 {
  font-size: 14pt;
}
.font18 {
  font-size: 18pt;
}

.float-right {
    float: right;
}

.text-align-right {
    text-align: right;
}

.overflow-wrap {
  overflow-wrap: anywhere;
}
.mt-2 {
  margin-top: 2rem;
}

.show-if-desktop,
.show-if-mobile{
  display: none !important;;
}

:root {
  --dark-grey: #46515f;
}

.dark-grey {
  color: var(--dark-grey);
}

.main-frame {
  background-color: #ffffff;
}

.header-frame {
  padding: 0.5rem;
  /*display: flex;*/
  align-items: center;
}

.card {
  /*box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);*/
  transition: 0.3s;
  border-radius: 5px;
  padding: .5rem 1rem 1rem;
  margin: 1rem 0;
  background-color: white;
}

.card-title {
  color: var(--dark-grey);
  font-size: 1.25rem;

}

.card-content {
  margin: 1rem 0;
  color: var(--dark-grey);
    width: 100%;
}

@media (min-width: 992px) {
  .main-frame {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-image: url(assets/img/bg-desktop.svg);
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .content-frame {
    padding: 2rem 9rem;
    background-color: white;
    border: solid 1px #b2b2b2;
    width: 30rem;
    height: auto;
  }

  .header-frame {
    flex-direction: column-reverse;
  }

  .logo {
    width: 84px;
    height: 64px;
  }
  .show-if-desktop{
    display: block !important;
  }
}

@media (max-width: 992px) {
  .logo {
    display: none;
  }
}

.date-info {
  font-size: 9pt;
  font-weight: normal;
  font-style: italic;
  text-align: end;
  margin-top: 1rem;
  margin-bottom: -10px;
}

.diagnostics-source-icon {
  color: var(--dark-grey);
  margin-right: .8rem;
}
